body {
  background-image: url("https://img.freepik.com/free-vector/simple-blue-curve-background-business_53876-117343.jpg?w=996&t=st=1708687879~exp=1708688479~hmac=5df55b604b43c44230d236a8e8a7d70eaf0cc3b2ee255598159817a1ad1569eb");
  background-size: cover;
}

.container {
  text-align: center;
  max-width: 400px;
  margin: 50px auto;
  font-family: Tahoma;
}

.event-form {
  margin-bottom: 20px;
}

.event-form label {
  margin-bottom: 5px;
  display: block;
}

.event-form input, .event-form textarea {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 80%;
  margin-bottom: 10px;
  padding: 8px;
}

.event-form button {
  color: #fff;
  cursor: pointer;
  background-color: #5482c0;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 14px;
}

.event-form button:hover {
  background-color: #0056b3;
}

.event-form .btn {
  color: #fff;
  cursor: pointer;
  background-color: #5482c0;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 14px;
}

.event-form .btn:hover {
  background-color: #0056b3;
}

.event-list {
  text-align: left;
}

.event-list .event-item {
  background-color: #f6f6f6;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
}

.event-list .event-item h2, .event-list .event-item p {
  margin-bottom: 5px;
}

